.header {
    position: relative;
    z-index: 1000;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 88px;
    background: var(--background);
    flex-shrink: 0;
}
.button {
    display: inline-flex;
    align-items: center;
    margin-left: 20px;
    cursor: pointer;

    color: var(--text-inactive-color)
}
.activebtn {
    display: inline-flex;
    align-items: center;
    margin-left: 20px;
    cursor: pointer;
    color: #FFF;
    fill: #fff;

    & span {
        color: #fff;
    }
    & svg {
        fill: #fff;
    }
}
.button:first-child, .activebtn:first-child {
    margin-left: 0;
}

.primary {
    /* color: var(--text-primary-color) */
}
/* .secondary {
    color: var(--text-inactive-color)
} */

.icon {
    display: inline-block;
    width: 24px;
    height: 24px;
    margin-right: 8px;
}

.navigator {
    position: relative;
    width: 1280px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.logo {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 290px;
    height: 50px;
    margin: auto;
}